.interface_menu-button {
  background-image: url("../../../assets/main.png");
  background-repeat: no-repeat;
  background-size: 100%;
  cursor: pointer;
  height: 45px;
  left: 30px;
  position: absolute;
  top: 30px;
  width: 45px;
}

.interface_menu-container {
  background-color: antiquewhite;
  border: 3px solid #000;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  left: 50%;
  margin: auto;
  position: absolute;
  padding: 25px;
  top: 50%;
  transform: translate(-50%, -50%);
}
.interface_menu-container > * {
  background-color: #008000;
  border: 3px solid #000;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  margin: 5px;
  padding: 5px;
  text-decoration: none;
}

.interface_menu-container__display-none {
  display: none !important;
}/*# sourceMappingURL=menu-button.css.map */